@import "./variables&functions";

*{
    margin: 0;
    padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
  
}

html{
  background-color: rgb(32,33,36);
  font-size: 1vw;
  li{
    list-style: none;
  }
}

.button{
  @include button;
}


.card{
    @include flexCustom(space-around, center , column);
    padding: 0px 1rem;
    border-radius: 1rem;
    text-align: center;
    width: 30%;
    height: 40%;
    background-color: $background;
    z-index: 99;
    @include absolutecenter;
  button{
    @include button;
}  
}


.tutorial{
  @include absolutecenter;
  color: white;
  background-color: $discordblue;
  padding: 1.6rem;
  width: 65%;
  height: 80%;
  z-index: 98;
  .tutorial__info{
    @include flexCenter(column);
  }
  .tutorial__gif{
    width: 30rem;
    height: 18rem;
    margin: 2rem;
    background-color: red;
    img{
      object-fit: contain;
      width: 100%;
      height: 100%
    }
  }
  div{
    @include flexCenter(column);
  }
  span{
    color: $red;
  }
  img{
    border-radius: 50%;
  }
  button{
    @include button;
  }
}


.game__wrapper{
  @include absolutecenter;
  background-color: $darkmode;
  width: 70%;
  height: 48rem;
  @include flexCenter(column);
  .game__wrapper--header{
    border: 2px solid black;
    background-color: $discordblue;
   width: 100%;
   height: 20%;
   @include flexCenter(column);
   div:first-child{
    background-color: inherit;
    padding-top: 1rem;
     button{
       @include button;
     }
   }
   .header__info{
     background-color: inherit;
     width: 100%;
     padding: 1rem;
        ul{
    background-color: inherit;
    @include flexCenter(row);
    width: 100%;
    li{
      background-color: $discordblue;
      font-size: 1.5rem;
      position: relative;
      text-align: center;
      width: 25%;
      
      span{
        background-color: inherit;
        position: absolute;
        right: 27% ;
        color: white;
      }
    }
  }
   }
}
}

#gamebody{
  background-color: $darkmode;
  display: grid;
  width: fit-content;
  padding: 1.5rem;
  margin: auto;
}

.box{
  height: 100%;
  width: 100%;
  text-align: center;
  border: 2px solid black;
  background-color: $boxColor;
  margin: auto;
  transition: background-Color 500ms ;
  animation: linear 1.5s ease-in;
}

@keyframes selected {
  50%{
    background-color: white;
  }
  100%{
background-color:  $boxColor;
  }
}

.gameOver{
  @include flexCenter(column);
  color: $mainTextColor;
  height: 15rem;
  width: 45rem;
  @include absolutecenter;
  background-color: $discordblue;
}

#r-b{
 animation: fade 1s linear;
}


@keyframes fade {
  0%{
    opacity: 0.4;
    transform: translateY(-1.5rem);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }
}

.leaderboard__wrapper{
  width: 100%;
  height: 100vh;
   @include flexCenter(row);
   align-items: center;
   .leaderboard__title{
    font-size: 1.5rem;
    border: 2px solid white;
    position: absolute;
    top: 5.5%;
    z-index: 100;
    padding:  0.5rem 2rem;
    background-color: $spotifyGreen;
    color: white;
    border-radius: 0.5rem;
    box-shadow: 0 10px 10px -2px $darkmode;
  }
}

.leaderboard{
  position: relative;
  background-color: $discordblue;
  border-radius: 1rem 0.5rem 0.5rem 1rem;
  height: 85%;
  width: 50rem;
  overflow-y: scroll;
  @include scrollbar;
  
  @include flexCenter(column);

  .leaderboard__position:first-child{
    
    margin-top: clamp(0rem, 20%, 40%);
  }
 
}


.leaderboard__position{
  
  div:first-child{
    width: 20%;
    @include flexCenter(row);
    img{
      border-radius: 50%;
      border: 2px solid;
      height: 3rem;
      width: 3rem;
    }
  }
  div:nth-child(2){
    span{
      color: black;
    }
    color: white;
    width: 80%;
    @include flexCenter(row);
    font-size: 1.2rem;
    background-color: $spotifyGreen;
    border-radius: 1rem;
    padding: 1rem;
  }

  width: 90%;
  margin-bottom: 3.5rem;
  @include flexCenter(row);
  justify-content: space-around;
}

.loadingAnimation{
  @include absolutecenter;
  @include flexCenter(column);
img{
  animation: loadingAnimation 3s infinite;
  width: 16rem;
}
h2{
  color: $spotifyGreen;
  margin-top: 2rem;
}
}

@keyframes loadingAnimation {
  0%{
  transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
  
}